@import './constants.scss';

.card {
  display: grid;
  width: 274px;
  height: 426px;
}

.card__thumb-img {
  position: relative;
  margin-bottom: 14px;

  & > img {
    width: 100%;
    height: 268px;

    object-fit: cover;

    border-radius: 14px;
  }

  & > button {
    position: absolute;
    z-index: 1;
    top: 14px;
    right: 14px;

    & > svg {
      stroke: $whiteColor;
      transition: stroke 0.5s;
    }

    & > svg.favorited {
      fill: $accentColor;
      stroke: $accentColor;
      transition: stroke 0.5s;
    }

    &:hover,
    &:focus {
      & > svg {
        stroke: $hoverColor;
      }

      & > svg.favorited {
        stroke: $whiteColor;
      }
    }
  }
}

.card__thumb-title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-right: 8px;
  margin-bottom: 8px;

  color: $headingsColor;

  & > h3 {
    font-size: $headingsFontSize;
    font-weight: 500;
    line-height: 1.33;

    & > span {
      color: $accentColor;
    }
  }

  & > p {
    font-size: $standartSize;
    font-weight: 500;
  }
}

.card__wrapper {
  & > ul {
    height: 40px;
    overflow: hidden;
  }
}

.card__btn {
  margin-top: 28px;
  width: 100%;
}
