@import './constants.scss';

.card-modal {
  display: grid;
  gap: 24px;

  padding: 0 24px 24px;
  width: 462px;

  color: $headingsColor;
}

.card-modal__img {
  width: 100%;
  height: 248px;
  object-fit: cover;

  margin-bottom: -10px;

  border-radius: 14px;
}

.card-modal__car-header {
  & > h3 {
    font-size: $headingsFontSize;
    font-weight: 500;
    line-height: 1.33;

    margin-bottom: 8px;

    & > span {
      color: $accentColor;
    }
  }

  & > ul {
    margin-bottom: 14px;
  }

  & > p {
    font-size: $headingsSecondFontSize;
    font-weight: 400;
    line-height: 1.42;
  }
}

.card-modal__accessories {
  & > h4 {
    font-size: $headingsSecondFontSize;
    font-weight: 500;
    line-height: 1.42;

    margin-bottom: 8px;
  }

  & > div {
    display: grid;
    gap: 4px;
  }
}

.card-modal__rental-conditions {
  & > h4 {
    font-size: $headingsSecondFontSize;
    font-weight: 500;
    line-height: 1.42;

    margin-bottom: 8px;
  }
}

.card-modal__btn {
  width: fit-content;
  font-size: $headingsSecondFontSize;
  font-weight: 600;
  line-height: 1.42;

  color: $whiteColor;
  background-color: $accentColor;

  border: none;
  border-radius: 12px;
  cursor: pointer;

  padding: 12px 50px;

  transition: background-color 0.5s;

  &:hover,
  &:focus {
    background-color: $hoverColor;
  }
}
